import { Controller } from '@ali/4ever-cangjie';
import onInsertImage from '../handlers/onInsertImage';

export default function createInsertImageFile(imageConfig, state) {
  return function insertImageFile(
    controller: Controller,
    maybeFiles: File | File[],
    nodeData?: Record<string, any>,
    onLoad?: () => void,
  ) {
    return onInsertImage(controller, maybeFiles, imageConfig, state, nodeData, onLoad);
  };
}
